export default {
	data() {
		return {
			//当前显示的页面
			currPage: 'home'
		}
	},
	onLoad: function() {
		uni.setNavigationBarTitle({
			title: "今日最新干货"
		})
	},
	onShow: function() {

	},
	methods: {
		//点击导航按钮切换页面
		changeNav(e) {
			this.currPage = e.currentTarget.dataset.curr;
			let currTitle = "UniAppLearning";
			switch (this.currPage) {
				case "home":
					currTitle = "今日最新干货";
					break;
				case "classify":
					currTitle = "分类";
					break;
				case "girl":
					currTitle = "妹子";
					break;
				case "mine":
					currTitle = "我的";



					break;
				default:
					break;
			}
			uni.setNavigationBarTitle({
				title: currTitle
			})
		},
		publishClick(e) {
			//发布按钮点击
			uni.navigateTo({
				url: '../publish/publish'
			})
		}
	},
}
